import React from "react";

import TypeAnimation from "../components/TypeAnimation";

import avatar from '../../images/about/avatar.png'

function About() {
    return (
        <section className="about PLA">
            <TypeAnimation text=" " className="headline"/>
            <div className="flex about__main">
                <section className="flex flex-column about__profile">
                    <img
                        src={avatar}
                        className="about__avatar"
                    />
                    <div className="flex flex-column about__me-box__info">
                        <div className="flex flex-column about__me-box">
                            <h2 className="about__name"> </h2>
                            <span className="about__nickname">@landerexit</span>
                            <span className="about__status"> </span>
                        </div>

                        <span className="about__info-line"><span className="about__info-line-index"> : </span> 04.03.2002</span>
                        <span className="about__info-line"><span className="about__info-line-index">: </span>, </span>
                        <span className="about__info-line"><span className="about__info-line-index">: </span> </span>
                        <span className="about__info-line"><span className="about__info-line-index">Telegram: </span>@landerexit</span>
                    </div>
                </section>
                <section className="about__bio">
                    <div className="about__bio-section">
                        <h3 className="about__bio-index"></h3>
                        <p className="about__bio-p">
                                Front-end Web-Developer'
                        </p>
                        <p className="about__bio-p">
                             Python,  JavaScript, TypeScript, React.js, MobX.js, SQL
                        </p>
                        <p className="about__bio-p">
                               git, github, npm, webpack, SASS
                        </p>
                        <p className="about__bio-p">
                                  CSS ( ,  ,     )
                        </p>
                        <p className="about__bio-p">
                                    ,   .       .
                        </p>
                    </div>
                    <div className="about__bio-section">
                        <h3 className="about__bio-index"> </h3>
                        <p className="about__bio-p">
                            Python  TypeScript  JavaScript  HTML+CSS      React  MobX  Webpack  Sass  Git  SQL  
                        </p>
                    </div>
                </section>
            </div>
        </section>
    )
}

export default About